import {Component} from '@angular/core';
import {IonicPage, NavController} from 'ionic-angular';
import {LsService} from "./ls.service";
import {UtilService} from "../../service/util-service/util.service";
import {PageLoginData} from "../../data.mapping";

/**
 * Generated class for the  page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-ls',
  template:
  `
  <ion-content>
    <h1></h1>
    <ion-grid class="grid-login-basic no-padding-lr">
      <ion-row justify-content-start align-items-center>
        <div class="w-auto">
          <ion-input class="login-tel" type="tel" placeholder="" [(ngModel)]="login.phoneno" (input)="format()"></ion-input>
        </div>
        <div>
          <button ion-button class="send-sms" (click)="sendSms()">{{timeText}}</button>
        </div>
      </ion-row>
      <ion-row justify-content-between align-items-center>
        <div class="w-auto">
          <ion-input class="login-code"  type="number" placeholder="" [(ngModel)]="login.verifycode" (input)="format()"></ion-input>
        </div>
        <div>
          <button ion-fab class="login-enter" [ngStyle]="{'opacity': opa }" (click)="signIn()">
            <img class="img-content-enter" src="./assets/imgs/xyb.png">
          </button>
        </div>
      </ion-row>
    </ion-grid>

    <div class="login-div" (click)="toLp()"></div>
    <div class="login-div" (click)="toR()"></div>

    <p class="text-agreement"> <a class="text-anchor" (click)="userAgreement()"></a>  <a class="text-anchor" (click)="userAgreement()"></a> </p>
  </ion-content>
  `
})
export class LsPage {
  statusBarColor: string = "#fff";

  login:PageLoginData = new PageLoginData();
  timeText:any = "";
  timer:any;
  opa:any = "0.4";

  constructor(public navCtrl: NavController,
              private util:UtilService,
              private lsService: LsService,) {
  }

  goBack() {
    this.navCtrl.pop();
  }

  userAgreement() {
    this.navCtrl.push('PPage');
  }

  toR() {
    this.navCtrl.push('RPage');
  }

  toLp() {
    this.navCtrl.push('LpPage');
  }

  sendSms(){
    if(this.checkPhone()){

      this.lsService.getSMSCode(this.login.phoneno).then(data => {
        console.log("" + JSON.stringify(data));
        //KEY 
        this.login.verifykey = data.verifykey;
        this.util.toastStart("",1500);

        this.timeText = 60;
        console.log("" + this.timeText + "");
        this.timer = setInterval(() => {
          this.timeText--;
          if (this.timeText <= 0) {
            clearTimeout(this.timer);
            console.log("");
            this.timeText = ""
          }
        }, 1000)

      }).catch(error => {
        clearTimeout(this.timer);
        this.timeText = "";
        this.util.toastStart("",1500);
      });

    }
  }

  signIn() {
    if(this.checkPhone()) {
      if (this.login.verifycode == null || this.login.verifycode == "") {     //
        this.util.toastStart("",1500);
      }else if(this.login.verifykey == null || this.login.verifykey == ""){
        this.util.toastStart("",1500);
      }else{
        this.util.loadingStart();

        this.lsService.login(this.login).then(data=> {
          if(!data || data.code != 0)
            throw  data;

          return this.lsService.getPersonMessage(data);
        }).then(data=>{
          if (data == "-1")
            throw data;

          return this.lsService.getOther();
        }).then(data=>{
          this.util.loadingEnd();
          this.navCtrl.setRoot('MPage');
        }).catch(error=>{
          this.util.loadingEnd();
          if(error && error.code && error.message != undefined && error.message != null && error.message != ""){
            console.log(error.message);
            this.util.toastStart(error.message,1500);
          }else{
            console.log("");
            this.util.toastStart("",1500);
          }
        });

      }
    }
  }

  checkPhone():boolean {
    if (!this.util.checkPhone(this.login.phoneno)){
      this.util.popoverStart("");
    }
    return this.util.checkPhone(this.login.phoneno);
  }

  format(){
    if(this.login.phoneno.length==11){
      if(this.checkPhone() && this.login.verifycode !="" && this.login.verifycode.length == 6){
        this.opa = "1";
      }else {
        this.opa = "0.4";
      }
    }
  }
}



