export class RestFulHeader {
  "Content-Type": string = "application/json";
  lt: string = "";//
  ai: string = "";//ID
  pi: string = "cn.sh.com.xj.timeApp";//ID
  pv: string = "v3";//
  di: string = "";//ID
  dt: string = "";//
  latitude: string = "0";//GPS
  longitude: string = "0";//GPS
}


export class UrlEntity {
  get desc(): string {
    return this._desc;
  }

  set desc(value: string) {
    this._desc = value;
  }
  private _url: string;
  private _key: string;
  private _desc: string;


  get url(): string {
    return this._url;
  }

  set url(value: string) {
    this._url = value;
  }

  get key(): string {
    return this._key;
  }

  set key(value: string) {
    this._key = value;
  }
}



