import { Component, ElementRef, QueryList, Renderer2, ViewChild, ViewChildren } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Scroll } from 'ionic-angular';
import { ControlAnchor, MapOptions, NavigationControlOptions, NavigationControlType } from 'angular2-baidu-map';
declare var BMap: any;

@IonicPage()
@Component({
  selector: 'page-dz',
  template: `
  <ion-content>
    <ion-searchbar (ionInput)="getItems($event)" placeholder="" animated="true"></ion-searchbar>
    <baidu-map [options]="options" (loaded)="maploaded($event)">
      <control type="navigation" [options]="navOptions"></control>
      <marker *ngFor="let marker of markers" [point]="marker.point" [options]="marker.options"></marker>
    </baidu-map>
    <ion-fab bottom center>
      <button ion-fab (click)="close()">
        <ion-icon name="checkmark"></ion-icon>
      </button>
    </ion-fab>
  </ion-content>
  `
})
export class DzPage {
  statusBarColor: string = "#fff";

  options: MapOptions;  //
  navOptions: NavigationControlOptions; //
  markers: Array<any> = new Array<any>();
  local: any;
  dz: string = "";

  constructor(public navCtrl: NavController,
              public navParams: NavParams) {
    if (this.navParams && this.navParams.data) {
      let value = this.navParams.data.value;

      if (value) {
        this.dz = value;
      }
    }

    //
    this.options = {
      centerAndZoom: {
        lat: 39.920116,
        lng: 116.403703,
        zoom: 12
      },
      enableScrollWheelZoom: true,
      enableKeyboard: true
    };

    //
    this.navOptions = {
  		anchor: ControlAnchor.BMAP_ANCHOR_BOTTOM_RIGHT,
  		type: NavigationControlType.BMAP_NAVIGATION_CONTROL_PAN
	  };

    this.markers.push({
      options: {
          icon: {
            imageUrl: 'assets/imgs/map/baidu_map_markers_2x.png',
            size: {
              height: 23,
              width: 25
            }
          },
          title: ''
        },
        point: {
          lat: 39.920116,
          lng: 116.403703
        }
    });
  }

  close() {
    this.navCtrl.pop();
  }

  getItems(ev) {
    console.log(ev);
    this.options['currentCity'] = '';
  }

  maploaded(e: any) {

  }

  search(e: any) { // baidu-maploaded emap
    let city: string = "";
  	//
  	this.local = new BMap.LocalSearch(city, {
    		renderOptions: {map: city, autoViewport: true, selectFirstResult: false},
    		pageCapacity: 10
  	});
  	// 
  	this.local.setSearchCompleteCallback(async (searchResults) => {
      	let markeSize = this.markers.length; //
      	for (let i = 0; i < markeSize; i++) {
       	 	e.removeOverlay(this.markers[i]);
      	}
      this.markers = [];
      if (typeof(searchResults) == "undefined" || typeof(searchResults.Qq) == "undefined") {		// 
        console.log("API");
        return;
      }
      let searchResult = [];
      searchResult = searchResults.Qq;		// searchResults.Lq
      let size = searchResult.length;
      let temp;
      let myIcon = new BMap.Icon("http://api.map.baidu.com/img/markers.png", new BMap.Size(23, 25), { // icon
        offset: new BMap.Size(10, 25),
        imageOffset: new BMap.Size(0, 0 - 10 * 25)
      });
      let contents = [];
      for (let i = 0; i < size; i++) {
        this.markers[i] = new BMap.Marker(new BMap.Point(searchResult[i].point.lng, searchResult[i].point.lat), {icon: myIcon});//
        //
        contents[i] = ":" + searchResult[i].title + "" + searchResult[i].address;// searchResult[i].point.lng, searchResult[i].point.lat
        this.markers[i].title = contents[i];
        //
        this.markers[i].addEventListener("click", () => {
          	var infoWindow = new BMap.InfoWindow("<p style='font-size:14px;'>" + contents[i] + "</p>");
          	this.markers[i].openInfoWindow(infoWindow);
        });// 
        e.addOverlay(this.markers[i]);// 
        if (i == 0) { // 
          var infoWindow = new BMap.InfoWindow("<p style='font-size:14px;'>" + contents[0] + "</p>");
          this.markers[0].openInfoWindow(infoWindow);
        }
      }
    });
    this.local.search(e);
  }
}



