import {Component} from '@angular/core';
import {IonicPage, NavController} from 'ionic-angular';
import {RService} from "./r.service";
import {UtilService} from "../../service/util-service/util.service";
import {DataConfig} from "../../service/config/data.config";
import {PageLoginData} from "../../data.mapping";

/**
 * Generated class for the  page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-r',
  template:
    `
    <ion-content>
      <h1></h1>
      <ion-grid class="grid-login-basic no-padding-lr">
        <ion-row justify-content-start align-items-center>
          <div class="w-auto">
            <ion-input class="register-tel" type="tel" placeholder="" [(ngModel)]="login.phoneno" (input)="format()"></ion-input>
          </div>
          <div>
            <button ion-button class="send-sms" (click)="sendSms()">{{timeText}}</button>
          </div>
        </ion-row>
        <ion-row justify-content-between align-items-center>
          <div class="w-auto">
            <ion-input class="register-code"  type="number" placeholder="" [(ngModel)]="login.verifycode" (input)="format()"></ion-input>
          </div>
        </ion-row>
        <ion-row justify-content-between align-items-center>
          <div class="w-auto">
            <ion-input class="register-name"  type="text" placeholder="" [(ngModel)]="login.username" (input)="format()"></ion-input>
          </div>
        </ion-row>
        <ion-row justify-content-between align-items-center>
          <div class="w-auto">
            <ion-input class="register-pwd" type="password" placeholder="" [(ngModel)]="login.userpassword" (input)="format()"></ion-input>
          </div>
          <div>
            <button ion-fab class="login-enter" [ngStyle]="{'opacity': opa }" (click)="register()">
              <img class="img-content-enter" src="./assets/imgs/xyb.png">
            </button>
          </div>
        </ion-row>
      </ion-grid>

     <div class="register-div" (click)="toLp()"></div>
      <div class="register-div" (click)="toLs()"></div>

      <p class="text-agreement"> <a class="text-anchor" (click)="userAgreement()"></a>  <a class="text-anchor" (click)="userAgreement()"></a> </p>
    </ion-content>
  `,
})
export class RPage {
  statusBarColor: string = "#fff";

  login: PageLoginData = new PageLoginData();
  timeText:any = "";
  timer:any;
  opa:any = "0.4";

  constructor(public navCtrl: NavController,
              private util:UtilService,
              private rService: RService,) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RPage');
  }

  goBack() {
    this.navCtrl.pop();
  }

  userAgreement() {
    this.navCtrl.push('PPage');
  }

  toLp() {
    this.navCtrl.push('LpPage');
  }

  toLs() {
    this.navCtrl.push('LsPage');
  }

  sendSms(){
    if(this.checkPhone()){
      this.rService.getSMSCode(this.login.phoneno).then(data => {
        //console.log("" + JSON.stringify(data));
        //KEY 
        this.login.verifykey = data.verifykey;
        this.util.toastStart("",1500);

        this.timeText = 60;
        console.log("" + this.timeText + "");
        this.timer = setInterval(() => {
          this.timeText--;
          if (this.timeText <= 0) {
            clearTimeout(this.timer);
            console.log("");
            this.timeText = ""
          }
        }, 1000)

      }).catch(error => {
        console.log("" + JSON.stringify(error));
        clearTimeout(this.timer);
        this.timeText = "";
        //this.util.toastStart("",1500);
      });

    }
  }

  register() {
    if(this.checkPhone()) {
      if (this.login.verifycode == null || this.login.verifycode == "") {     //
        this.util.toastStart("",1500);
      }else if (this.login.username == null || this.login.username == "") {           //
        this.util.toastStart("",1500);
      }else if (this.login.userpassword == null || this.login.userpassword == "") {     //
        this.util.toastStart("",1500);
      }else if(this.login.verifykey == null || this.login.verifykey == ""){
        this.util.toastStart("",1500);
      }else {
        this.util.loadingStart();
        this.rService.register(this.login).then(data => {
          clearTimeout(this.timer);
          this.util.loadingEnd();
          this.navCtrl.setRoot(DataConfig.PAGE._M_PAGE);
        }).catch(error=>{
          this.util.loadingEnd();
          if(error && error.code && error.message != undefined && error.message != null && error.message != ""){
            console.log(error.message);
            this.util.toastStart(error.message,1500);
          }else{
            console.log("");
            this.util.toastStart("",1500);
          }
        });
      }
    }
  }

  checkPhone():boolean {
    if (!this.util.checkPhone(this.login.phoneno)){
      this.util.toastStart("",1500);
    }
    return this.util.checkPhone(this.login.phoneno);
  }

  format(){
    if(this.login.phoneno.length==11){
      if(this.checkPhone()
        && this.login.verifycode !="" && this.login.verifycode.length == 6
        && this.login.username !="" && this.login.username.length >= 2
        && this.login.userpassword !="" && this.login.userpassword.length >= 4){
        this.opa = "1";
      }else {
        this.opa = "0.4";
      }
    }
  }

}



