import { Component, ElementRef, QueryList, Renderer2, ViewChild, ViewChildren } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Scroll } from 'ionic-angular';
import {Keyboard} from "@ionic-native/keyboard";
import { RadioSelectComponent } from "../../components/radio-select/radio-select";
import { RadioSpinnerComponent } from "../../components/radio-spinner/radio-spinner";
import { DatePickerComponent } from "../../components/date-picker/date-picker";
import * as moment from "moment";

@IonicPage()
@Component({
  selector: 'page-cf',
  template: `
  <ion-content>
    <ion-grid class="h100">
      <ion-row justify-content-center>
        <p class="title">{{title}}</p>
      </ion-row>
      <ion-row justify-content-center>
        <radio-select [options]="items" center="true" [(ngModel)]="cfType" (onChanged)="onTypeChanged($event)"></radio-select>
      </ion-row>
      <!--  -->
      <ion-row justify-content-center *ngIf="cfType == 'day'">
        <ion-grid class="ph15">
          <ion-row justify-content-start>
            <p></p>
          </ion-row>
          <ion-row justify-content-start>
            <radio-spinner label="" [options]="itemRanges" [(ngModel)]="cfDayOptions.frequency" (onChanged)="onFreqChanged($event)"></radio-spinner>
          </ion-row>
        </ion-grid>
      </ion-row>
      <ion-row justify-content-center *ngIf="cfType == 'day'">
        <ion-grid class="ph15">
          <ion-row justify-content-start>
            <p></p>
          </ion-row>
          <ion-row justify-content-start>
            <ion-list class="endwith" radio-group no-lines [(ngModel)]="cfDayOptions.endType" (ionChange)="onEndTypeChanged($event)">
              <ion-item>
                <ion-radio item-start value="never"></ion-radio>
                <ion-label></ion-label>
              </ion-item>
              <ion-item>
                <ion-radio item-start value="aftertimes"></ion-radio>
                <ion-label class="inline">
                  <div *ngIf="cfDayOptions.endType != 'aftertimes'" class="inlabel"></div>
                  <radio-spinner label="" *ngIf="cfDayOptions.endType == 'aftertimes'" [options]="itemRanges" [(ngModel)]="cfDayOptions.afterTimes" (onChanged)="onEndAfterTimesChanged($event)"></radio-spinner>
                </ion-label>
              </ion-item>
              <ion-item>
                <ion-radio item-start value="tosomeday"></ion-radio>
                <ion-label></ion-label>
              </ion-item>
            </ion-list>
          </ion-row>
        </ion-grid>
      </ion-row>
      <!--  -->
      <ion-row justify-content-center *ngIf="cfType == 'week'">
        <ion-grid class="ph15">
          <ion-row justify-content-start>
            <p></p>
          </ion-row>
          <ion-row justify-content-start>
            <radio-spinner label="" [options]="itemRanges" [(ngModel)]="cfWeekOptions.frequency" (onChanged)="onFreqChanged($event)"></radio-spinner>
          </ion-row>
        </ion-grid>
      </ion-row>
      <ion-row justify-content-center *ngIf="cfType == 'week'">
        <ion-grid class="ph15">
          <ion-row justify-content-start>
            <p></p>
          </ion-row>
          <ion-row justify-content-start>
            <radio-select [options]="itemRangeOptions" [(ngModel)]="cfWeekOptions.freqOption" (onChanged)="onFreqOptionChanged($event)"></radio-select>
          </ion-row>
        </ion-grid>
      </ion-row>
      <ion-row justify-content-center *ngIf="cfType == 'week'">
        <ion-grid class="ph15">
          <ion-row justify-content-start>
            <p></p>
          </ion-row>
          <ion-row justify-content-start>
            <ion-list class="endwith" radio-group no-lines [(ngModel)]="cfWeekOptions.endType" (ionChange)="onEndTypeChanged($event)">
              <ion-item>
                <ion-radio item-start value="never"></ion-radio>
                <ion-label></ion-label>
              </ion-item>
              <ion-item>
                <ion-radio item-start value="aftertimes"></ion-radio>
                <ion-label class="inline">
                  <div *ngIf="cfWeekOptions.endType != 'aftertimes'" class="inlabel"></div>
                  <radio-spinner label="" *ngIf="cfWeekOptions.endType == 'aftertimes'" [options]="itemRanges" [(ngModel)]="cfWeekOptions.afterTimes" (onChanged)="onEndAfterTimesChanged($event)"></radio-spinner>
                </ion-label>
              </ion-item>
              <ion-item>
                <ion-radio item-start value="tosomeday"></ion-radio>
                <ion-label></ion-label>
              </ion-item>
            </ion-list>
          </ion-row>
        </ion-grid>
      </ion-row>
      <!--  -->
      <ion-row justify-content-center *ngIf="cfType == 'month'">
        <ion-grid class="ph15">
          <ion-row justify-content-start>
            <p></p>
          </ion-row>
          <ion-row justify-content-start>
            <radio-spinner label="" [options]="itemRanges" [(ngModel)]="cfMonthOptions.frequency" (onChanged)="onFreqChanged($event)"></radio-spinner>
          </ion-row>
        </ion-grid>
      </ion-row>
      <ion-row justify-content-center *ngIf="cfType == 'month'">
        <ion-grid class="ph15">
          <ion-row justify-content-start>
            <p></p>
          </ion-row>
          <ion-row justify-content-start>
            <radio-select [options]="itemRangeOptions" [(ngModel)]="cfMonthOptions.freqOption" (onChanged)="onFreqOptionChanged($event)"></radio-select>
          </ion-row>
        </ion-grid>
      </ion-row>
      <ion-row justify-content-center *ngIf="cfType == 'month'">
        <ion-grid class="ph15">
          <ion-row justify-content-start>
            <p></p>
          </ion-row>
          <ion-row justify-content-start>
            <ion-list class="endwith" radio-group no-lines [(ngModel)]="cfMonthOptions.endType" (ionChange)="onEndTypeChanged($event)">
              <ion-item>
                <ion-radio item-start value="never"></ion-radio>
                <ion-label></ion-label>
              </ion-item>
              <ion-item>
                <ion-radio item-start value="aftertimes"></ion-radio>
                <ion-label class="inline">
                  <div *ngIf="cfMonthOptions.endType != 'aftertimes'" class="inlabel"></div>
                  <radio-spinner label="" *ngIf="cfMonthOptions.endType == 'aftertimes'" [options]="itemRanges" [(ngModel)]="cfMonthOptions.afterTimes" (onChanged)="onEndAfterTimesChanged($event)"></radio-spinner>
                </ion-label>
              </ion-item>
              <ion-item>
                <ion-radio item-start value="tosomeday"></ion-radio>
                <ion-label></ion-label>
              </ion-item>
            </ion-list>
          </ion-row>
        </ion-grid>
      </ion-row>
      <!--  -->
      <ion-row justify-content-center *ngIf="cfType == 'year'">
        <ion-grid class="ph15">
          <ion-row justify-content-start>
            <p></p>
          </ion-row>
          <ion-row justify-content-start>
            <radio-spinner label="" [options]="itemRanges" [(ngModel)]="cfYearOptions.frequency" (onChanged)="onFreqChanged($event)"></radio-spinner>
          </ion-row>
        </ion-grid>
      </ion-row>
      <ion-row justify-content-center *ngIf="cfType == 'year'">
        <ion-grid class="ph15">
          <ion-row justify-content-start>
            <p></p>
          </ion-row>
          <ion-row justify-content-start>
            <ion-list class="endwith" radio-group no-lines [(ngModel)]="cfYearOptions.endType" (ionChange)="onEndTypeChanged($event)">
              <ion-item>
                <ion-radio item-start value="never"></ion-radio>
                <ion-label></ion-label>
              </ion-item>
              <ion-item>
                <ion-radio item-start value="aftertimes"></ion-radio>
                <ion-label class="inline">
                  <div *ngIf="cfYearOptions.endType != 'aftertimes'" class="inlabel"></div>
                  <radio-spinner label="" *ngIf="cfYearOptions.endType == 'aftertimes'" [options]="itemRanges" [(ngModel)]="cfYearOptions.afterTimes" (onChanged)="onEndAfterTimesChanged($event)"></radio-spinner>
                </ion-label>
              </ion-item>
              <ion-item>
                <ion-radio item-start value="tosomeday"></ion-radio>
                <ion-label></ion-label>
              </ion-item>
            </ion-list>
          </ion-row>
          <ion-row justify-content-start *ngIf="cfYearOptions.endType == 'tosomeday'">
            <date-picker min="2019/7/9" max="2020/12/31"></date-picker>
          </ion-row>
        </ion-grid>
      </ion-row>
    </ion-grid>
  </ion-content>

  <ion-footer class="foot-set">
    <ion-toolbar>
    <button ion-button full (click)="close()">
      
    </button>
    </ion-toolbar>
  </ion-footer>
  `
})
export class CfPage {
  statusBarColor: string = "#3c4d55";

  items: Array<any> = new Array<any>();
  itemRanges: Array<any> = new Array<any>();
  itemRangeOptions: Array<any> = new Array<any>();

  title: string = "";
  cfType: string = "";
  //
  cfDayOptions: any = {
    frequency: "",
    freqOption: "",
    endType: "never",
    afterTimes: 1,
    toSomeDay: moment().format("YYYYMD")
  };
  //
  cfWeekOptions: any = {
    frequency: "",
    freqOption: "",
    endType: "never",
    afterTimes: 1,
    toSomeDay: moment().format("YYYYMD")
  };
  //
  cfMonthOptions: any = {
    frequency: "",
    freqOption: "",
    endType: "never",
    afterTimes: 1,
    toSomeDay: moment().format("YYYYMD")
  };
  //
  cfYearOptions: any = {
    frequency: "",
    freqOption: "",
    endType: "never",
    afterTimes: 1,
    toSomeDay: moment().format("YYYYMD")
  };

  optionCaptions: any = {
    sunday: "",
    monday: "",
    tuesday: "",
    wednesday: "",
    thursday: "",
    friday: "",
    saturday: "",
  };

  constructor(public navCtrl: NavController,
              private keyboard: Keyboard,
              public navParams: NavParams) {
    if (this.navParams && this.navParams.data) {
      let value = this.navParams.data.value;

      if (value) {
      }
    }
    this.items.push({value: "", caption: ""});
    this.items.push({value: "day", caption: ""});
    this.items.push({value: "week", caption: ""});
    this.items.push({value: "month", caption: ""});
    this.items.push({value: "year", caption: ""});

    this.itemRanges.push({value: "subtract", icon: "remove"});
    this.itemRanges.push({value: "add", icon: "add"});

    this.itemRangeOptions.push({value: "sunday", caption: ""});
    this.itemRangeOptions.push({value: "monday", caption: ""});
    this.itemRangeOptions.push({value: "tuesday", caption: ""});
    this.itemRangeOptions.push({value: "wednesday", caption: ""});
    this.itemRangeOptions.push({value: "thursday", caption: ""});
    this.itemRangeOptions.push({value: "friday", caption: ""});
    this.itemRangeOptions.push({value: "saturday", caption: ""});
  }

  ionViewDidEnter() {

  }

  close() {
    this.navCtrl.pop();
  }

  private getFreqTitle(title: string, option: any) {
    let freqtitle = title;

    if (option.frequency) {
      freqtitle = (option.frequency == 1? "" : option.frequency) + freqtitle;

      if (option.freqOption) {
        let caption = this.optionCaptions[option.freqOption];
        freqtitle += " " + (caption? caption : "");
      }
    }

    return freqtitle;
  }

  private getEndTitle(option: any) {
    let endtitle = "";

    switch (option.endType) {
      case "never":
        endtitle = "";
        break;
      case "aftertimes":
        endtitle = option.afterTimes + "";
        break;
      case "tosomeday":
        endtitle = " " + option.toSomeDay;
        break;
      default:
        break;
    }

    return endtitle;
  }

  resetTitle(cfType: string) {
    switch (cfType) {
      case "day":
        this.title = " " + this.getFreqTitle("", this.cfDayOptions) + ", " + this.getEndTitle(this.cfDayOptions);
        break;
      case "week":
        this.title = " " + this.getFreqTitle("", this.cfWeekOptions) + ", " + this.getEndTitle(this.cfWeekOptions);
        break;
      case "month":
        this.title = " " + this.getFreqTitle("", this.cfMonthOptions) + ", " + this.getEndTitle(this.cfMonthOptions);
        break;
      case "year":
        this.title = " " + this.getFreqTitle("", this.cfYearOptions) + ", " + this.getEndTitle(this.cfYearOptions);
        break;
      default:
        this.title = "";
        break;
    }
  }

  onTypeChanged(value) {
    this.resetTitle(value);
  }

  onFreqChanged() {
    this.resetTitle(this.cfType);
  }

  onFreqOptionChanged() {
    this.resetTitle(this.cfType);
  }

  onEndTypeChanged(value) {
    this.resetTitle(this.cfType);
  }

  onEndAfterTimesChanged(value) {
    this.resetTitle(this.cfType);
  }
}



